import React, { useState } from 'react'
import Button from '@mui/material/Button';
import Box from '@mui/material/Box'
import {Formik,Form,Field} from 'formik'
import { TextField, Typography } from '@mui/material';

import Card from '@mui/material/Card';
import axios from 'axios';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';

const Adminpenal = () => {
  const [ini,setIni] = useState({
    email:'',
    password:'',
  })
  const history =useHistory()
  const handleSubmit=(values)=>{
    console.log(values)
    
    axios.post('https://interviewback-ucb4.onrender.com/admin/login',values)
    .then(()=>{
      history.push('/dashboard')
      console.log('succes')
    })
    .catch((err)=>{
      console.log('err')
    })
  }
  return (
   <>
    
    <Box sx={{height:'800px',justifyContent:'center',display:'flex',alignItems:'center'}}>
    <Card sx={{ 
      minHeight: '300px', 
      width:  { lg:380,md:380,sm:380,xs:280},
      border:'2px solid #1976D2',
      borderRadius:'25px'
      }}>
        <Box  sx={{ padding: {lg:'25px',md:'20px',sm:'10px',xs:'10px'},justifyItems:{lg:'center',md:'center',sm:'center',xs:'center'} }}>
          <Typography variant="h4" sx={{color:'#1976D2',fontWeight:'700'}}>Admin Panel</Typography> <br/>
            <Formik
            enableReinitialize
            initialValues={ini}
            onSubmit={handleSubmit}
            >
              <Form>
                <Box sx={{ justifyItems:'center'}}>
                  <Field as={TextField} sx={{width: { lg:330,md:330,sm:330,xs:200}}} name='email' placeholder='Email'></Field><br/> <br/>
                  <Field as={TextField} sx={{width: { lg:330,md:330,sm:330,xs:200}}} name='password' placeholder='Password'></Field> <br/> <br/>
                  <Button type='submit' variant="contained" sx={{display:'flex'}}>Submit</Button>
                </Box>
              </Form>
            </Formik>
        </Box>
    </Card>
    </Box>
   
   </>
  )
}

export default Adminpenal